.carousel-container {
    display: flex;
    align-items: center;
    justify-content: center;
    position:absolute;
    top: 5500px;
    left:3%;
}

.carousel-images-container {
    display: flex;
    align-items: center;
    overflow: hidden; /* Adjust if necessary */
}

.carousel-image {
    border-radius: 10%;
    border: 1px solid black;
    margin-right: 20px;
    margin-left: 20px; /* Adjust the space between images */
    object-fit: cover; /* Adjust as needed */
}

.carousel-arrow {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 24px; /* Adjust size as needed */
}

/* Add additional styling for arrows if needed */
