`.parent-container {
    position: relative; /* This container is positioned relative to allow absolute positioning inside */
    display: flex;
    align-items: center;
}

.circle {
    width: 1800px;
    height: 1800px;
    border-radius: 50%;
    border: 8px solid black;
    position: absolute; /* Positioned absolutely within the parent-container */
    z-index: 2;
}

