.custom-button {
    border: 1px solid black;
    border-radius: 20px;
    background-color: transparent;
    color: black;
    padding: 26px 26px;
    font-size: 130%;
    transition: background-color 0.3s, color 0.3s, font-size 0.5s ease-in-out;
    cursor: pointer;
    text-align: center;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 275px;
    height: 100px;
    position: absolute; /* Use absolute positioning */
    /* Removed the margin-left and replaced with props based positioning */
}

    .custom-button:hover {
        background-color: lightslategray;
    }
