.custom-contact-form-container {
    position: absolute;
    top: 2000px;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    padding-top: 120px;
    padding-left: 22vw;
    padding-right: 50px;
    height: auto;
    background-color: transparent;
}

.custom-form-inputs {
    width: calc(50% - 40px);
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-bottom: 20px;
}

    .custom-form-inputs input,
    .custom-form-inputs textarea {
        max-width: 450px;
        width:calc(30vw - 110px);
        resize: none;
        padding: 10px;
        border: 1px solid #ccc;
        border-radius: 5px;
    }

.custom-buttons-containerC {
    padding-right: 25vw;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 10px;
    width: fit-content;
    padding-bottom: 40px;
}

.custom-buttons-rowC {
    display: flex;
    justify-content: space-around;
    gap: calc(5vw - 60px);
    padding-top:10px;
    width: 100%;
}

.custom-buttonC {
    flex: 0 0 auto;
    width: 100px;
    padding: 10px;
    background-color: transparent;
    color: black;
    border: 1px solid black;
    border-radius: 13px;
    cursor: pointer;
    text-align: center;
}

.custom-buttonC-toggled {
    background-color: black;
    color: white;
}
    .custom-buttonC:hover {
        background-color: black;
        color: white;
    }



@media (max-width: 1300px) {
    .custom-contact-form-container {
        top: 1650px;
    }

    .custom-buttons-containerC {
        padding-right:15vw;
    }
    .custom-buttons-rowC {
        gap:5px;
    }

    .custom-form-inputs input,
    .custom-form-inputs textarea {
        width: calc(40vw - 160px);

    }
}

@media (max-width: 1100px) {
    .custom-contact-form-container {
        top: calc(90vw + 2400px);
        padding-left: 15vw;
        padding-right: 0px;
        height: auto;
    }
    .custom-buttons-rowC {
        padding-top: 10px;
    }

    .custom-buttons-containerC {
        padding-right: 12vw;
    }

    .custom-form-inputs input,
    .custom-form-inputs textarea {
        width: calc(70vw - 340px);

    }
}



@media (max-width: 900px) {
    .custom-contact-form-container {
        overflow:hidden;
        padding-top: 170px;
        flex-direction: column; /* Stack input form and buttons vertically */
        align-items: center; /* Center align the children */
        top: calc(80vw + 2500px);
        padding-left: 40px; /* Adjust padding as needed for smaller screens */
        padding-right: 40px; /* Adjust padding as needed for smaller screens */
    }

    .custom-form-inputs,
    .custom-buttons-containerC {
        width: 100%;
        /* Use full width for inputs and buttons */
    }

        .custom-form-inputs input,
        .custom-form-inputs textarea {
            width:95%;
            max-width: 1000px;
        }



        .custom-buttons-containerC {
            padding-top: 100px;
            align-items: center; /* Center align buttons */
            padding-left:13%;
        }

    .custom-buttons-rowC {
        justify-content: center; /* Center align the buttons within each row */
        gap: 2vw; /* Adjust gap between buttons as needed */
    }

    .custom-buttonC {
        width: calc(100% / 3 - 20px); /* Adjust button width to fit 3 per row, or as desired */
    }
}
