.container {
    display: flex;
    align-items: center;
    position: relative;
    max-width: 90%; /* Adjust based on your layout requirements */
}

.text {
    flex-grow: 1;
    max-height: 200px; /* Limit the height of the text container */
    overflow-y: visible; /* Enable scrolling for overflow */
    padding-right: 700px; /* Custom right padding */
    padding-left: 100px; /* Custom left padding */
    margin-top: -150px; /* Custom top padding */
    margin-right: 200px; /* Ensure text doesn't overlap the image */
    font-size:23px;
    letter-spacing:0.5px;
    word-spacing:2px;
}

