.fullscreen-image-container {
    position: relative;
    width: 100%;
    height: 100vh; /* Full screen height */
    background-size: cover;
    background-position: center;
}

.gradient-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(180deg, transparent, rgba(0,0,0,0.7)); /* Black gradient effect */
    display: flex;
    justify-content: center;
    align-items: center;
}

.centered-text {
    color: white;
    text-align: center;
    font-size: 2rem; /* Adjust size as needed */
    padding: 0 20px; /* Ensures text doesn't touch the sides */
}
