
.quem-somos {
    height: 2750px;
}



@media (max-width: 1300px) {
    .quem-somos {
        height: 2350px;
    }
}

@media (max-width: 1100px) {
    .quem-somos {
        height: calc(90vw + 3200px);
    }
}

@media (max-width: 900px) {
    .quem-somos {
        height: calc(90vw + 3550px);
    }

}

@media (max-width: 650px) {
    .quem-somos {
    }
}
