.QS-images-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    z-index: 3; /* Ensure images are above the circle */
    overflow-x:hidden;
}


.quem-somos-image2 {
    border: 2px solid black;
    width: 700px;
    height: auto;
    border-radius: 10px;
    object-fit: cover;
    position: relative;
    margin-top: 100px;
    right:-30px;
}

